/* 2. GENERAL                */
/*===============================================*/ 
html, body{
  width: 100%; 
  height:100%;
}
body{
  font-family: 'Source Sans Pro', sans-serif;
  padding:0;
  font-size:16px;
  margin:0 auto;
  color:#1d1d1d;
  background-color:#fff;
  font-weight:300;
}
.clear{
  clear:both;
}
p{
  padding:0;
  margin:0px;
  text-align:left;
  line-height:26px;
}
a{
  text-decoration:none;
  color:#82c064; 
}
blockquote{
  font-style:italic;
  font-size:24px;
  padding:0 5%;
  margin:40px 0 60px 0;
  line-height:34px;
  position:relative;
  text-align:center;
}
blockquote:after {
    content:' ';
    position: absolute;
    display:block;
    width: 10%;
    margin: 20px 40%;
    border:1px solid #e76b71;
} 
a img{
  border:none;
}
a:hover{
  text-decoration:none;
  color:#212121;
}
img.responsive_image{
  max-width:100%;
  display:block;
  margin:auto auto 20px auto;
}
/*---------------------------*/
/* 2-1. HEADINGS             */
/*---------------------------*/ 
h1, h2, h3, h4, h5, h6{
  font-weight:700;
  letter-spacing:-0.5px;
  padding:0 0 40px 0;
  margin:0;
}
h1{
  font-size:40px;
}
h2{
  font-size:30px;
}
h3{
  font-size:22px;
}
h4{
  font-size:20px;
}
h5{
  font-size:18px;
}
h6{
  font-size:16px;
}
/*===============================================*/
/* 3. HEADER               */
/*===============================================*/
/* BLOCK - header */
.header{
  width:100%;
  height:70px;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  position: fixed;
  top: 0px;
  left:0px;
  z-index: 999;
}
/* MODIFIER - header */
.header--sticky{
  background-color:#fff;
    box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
    -moz-box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
    -webkit-box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
}
/* ELEMENT - header__content */
.header__content{
  height:70px;
}
/* ELEMENT MODIFIER - header__content */
.header__content--fix-width{
  width:1240px;
  margin:auto;
  clear:both;
}
.header__content--fluid-width{
  width:80%;
  margin:auto;
  clear:both;
}
.header__logo-title{
  color:#000;
  font-size:26px;
  letter-spacing:-1px;
  position:relative;
  float:left;
  top: 50%;
  left:0;
  transform: translateY(-50%);
  z-index:999;
}

.header__logo-title span{
  font-weight:700;
}
/* ELEMENT - header__menu */
.header__menu {
  float:right;
}
.header__menu ul,
.header__menu ul li,
.header__menu ul li a {
  margin: 0;
  padding: 0;
  border: 0;
  list-style: none;
  line-height: 1;
  display: block;
  float:left;
  position: relative;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}
.header__menu:after,
.header__menu > ul:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
.menu-button {
  width:28px;
  height:20px;
  position: absolute;
  padding: 0px;
  font-size:20px;
  top:12px;
  right:6%;
  z-index:99999;
  display: none;
  cursor:pointer;
  border-bottom:1px solid #fff;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}
.menu-button:before {
  content: "";
  position: absolute;
  top: 6px;
  left: 0;
  width: 28px;
  height: 6px;
  z-index:999;
  border-top: 1px double #fff;
  border-bottom: 1px solid #fff;
}
.header__menu > ul > li {display:inline-block; padding:0; }
.header__menu > ul > li > a {
    height:70px;
  line-height:70px; 
  color:#fff; 
  font-size:14px;
  display:inline-block;
  letter-spacing: 0.5px;
  padding:0 25px; 
  margin:0;
  font-weight:400;
  overflow: hidden;
}
.header--sticky .header__menu > ul > li > a, .header--page .header__menu > ul > li > a {
  color:#000; 
}
.header--sticky .menu-button, .menu-open .menu-button{
  border-bottom:1px solid #000;
}
.header--sticky .menu-button:before, .menu-open .menu-button:before{
  border-bottom:1px solid #000;
  border-top: 1px double #000;
}
.header__menu > ul > li.header__btn > a {
    height:32px;
  line-height:30px; 
  -webkit-border-radius: 25px; 
  -moz-border-radius: 25px; 
  border-radius: 25px; 
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.header__menu > ul > li.header__btn--signup > a { 
  color:#fff; 
  background-color:#ff8d12;
  padding:0 22px; 
  margin:19px 0 0 25px;
  border:1px #ff8d12 solid;
}
.header__menu > ul > li.header__btn--login > a {
  color:#fff; 
  padding:0 22px; 
  margin:19px 0 0 25px;
  border:1px #fff solid;
}
.header--sticky .header__menu > ul > li.header__btn--login > a, .menu-open .header__menu > ul > li.header__btn--login > a {
  color:#272b26; 
  border:1px #272b26 solid;
}

.header__menu ul li.header__btn--signup > a:hover{
  background:none; 
  border:1px #fff solid;
  color:#fff;
}
.header--sticky .header__menu ul li.header__btn--signup > a:hover, .menu-open .header__menu ul li.header__btn--signup > a:hover{
  background:none; 
  border:1px #ff8d12 solid;
  color:#ff8d12;
}
.header__menu ul li.header__btn--login > a:hover{
  background-color:#fff; 
  color:#272b26;
}
.header--sticky .header__menu ul li.header__btn--login > a:hover{
  background-color:#fff; 
  border:1px #82c064 solid;
  color:#82c064;
}
.header__menu ul li  a:hover, .header__menu ul li a.selected , .header__menu ul li a.active{
  color: #272b26;
}
.header--sticky .header__menu ul li  a:hover, .header--sticky .header__menu ul li a.selected , .header--sticky .header__menu ul li a.active{
  color: #ff8d12;
}
.header__menu ul li.header__btn--signup a.active { 
  color:#fff; 
}
.header__menu ul li.header__btn--login a.active {
  color:#272b26; 
}
.header__menu ul li:hover{
  cursor:pointer;
}
.header__menu > ul > li.menu-item-has-children > a{
  background:url(/images/drop-down-white.png) no-repeat 90% 50%;
}
.header--sticky .header__menu > ul > li.menu-item-has-children > a{
  background:url(/images/drop-down.png) no-repeat 90% 50%;
}
.header__menu ul ul {
  position: absolute;
  left: 0px;
  top:55px;
  z-index:9999;
  visibility: hidden;
  text-align:left;
  padding:5px 0;
  background-color:#fff;
  -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.10);
  -moz-box-shadow:    0px 0px 10px 1px rgba(0,0,0,0.10); 
  box-shadow:         0px 0px 10px 1px rgba(0,0,0,0.10);
  -webkit-border-radius: 5px; 
  -moz-border-radius: 5px; 
  border-radius: 5px;  
  opacity:0;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.header__menu ul ul.submenu-header-sticky{
    box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
    -moz-box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
    -webkit-box-shadow: 0 4px 10px 0px rgba(0,0,0,0.03);
   border-top-left-radius:0px;
   border-top-right-radius:0px;
   -webkit-border-top-left-radius:0px;
   -webkit-border-top-right-radius:0px;
}
.header__menu li:hover > ul {   
  visibility: visible;
  opacity: 1;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  top:70px;
}
.header__menu ul ul ul {
  margin-left: 100%;
  top: 0;
}
.header__menu ul ul li a {
  width:200px;
  padding:0 15px;
  color:#231f20;   
  border-bottom:1px #f7f8f9 solid;
  margin:0 10px;
  font-size:14px;
  line-height:45px; height:45px;
}
.header__menu ul ul li a:hover{ color: #272b26; }
.header__menu ul ul li:last-child a {
 border-bottom:none; 
 }
