/* 4. SECTION                */
/*===============================================*/
/* BLOCK - section */
.section{
  width:100%;
  height:auto;
  float:left;
  clear:both;
}
/* BLOCK MODIFIER - section--page */
.section--page-top{
  padding-top:80px;
}
.section--page-bottom{
  padding-bottom:80px;
}
/* ELEMENT - section__content */
.section__content{
  clear:both;
}
/* ELEMENT MODIFIER section__content  */
.section__content--fix-width{
  width:1240px;
  margin:auto;
  clear:both;
}
.section__content--fluid-width{
  width:80%;
  margin:auto;
  clear:both;
}
.section__content--full-width{
  width:98%;
  margin:auto;
  clear:both;
}
.section__content--padding{
  padding:100px 0;
}
/* ELEMENT - section__title */
.section__title{
  font-size:48px;
  padding:0 0 40px 0;
  font-weight:700;
  line-height:52px;
}
/* ELEMENT MODIFIER section__title  */
.section__title--centered{
  width:100%;
  text-align:center;
  position:relative;
}
.section__title--centered:after {
    content:'';
    position:absolute;
    left:0;right:0;
    top:80%;
    margin:auto;
    width:50px;
    height:1px;
    background:#454545;
}

/* ELEMENT - section__description */
.section__description{
  font-size:18px;
  line-height:32px;
}
/* ELEMENT MODIFIER section__description  */
.section__description--centered{
  width:60%;
  margin:auto;
  text-align:center;
  padding:0 0 80px 0;
}
