  margin:0px;
}
.pricing__title:after {
    content:'';
    position:absolute;
    left:0;right:0;
    top:60%;
    margin:auto;
    width:50px;
    height:1px;
    background:#454545;
  -webkit-transition: all .5s ease-out;
