    background:#fff;
  top:70%;
}
.pricing__plan:hover .pricing__title:after {
    width:80%;
} 
.pricing__values{
  font-size:58px;
  font-weight:700;
  height:70px;
  position:relative;
  text-align:center;
}
.pricing__value span{
  display:inline-block;
  font-size:20px;
  margin:0px 0 0 0;
}
.pricing__value b{
  display:inline-block;
  font-size:18px;
  font-weight:700;
  position:relative;
  bottom:-5px;
}
.pricing__value{
  position:absolute;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
}
.pricing__value--hidden{
  display:none;
}
.pricing__value--show{
  -webkit-animation: showToY 0.6s forwards;
  animation: showToY 0.6s forwards;
}
.pricing__value--hide{
  -webkit-animation: fadeToY 0.6s forwards;
  animation: fadeToY 0.6s forwards;
}
@keyframes fadeToY {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(80%);
    transform: translateY(80%);
  }
}
@-webkit-keyframes fadeToY {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(80%);
    transform: translateY(80%);
  }
}
@keyframes showToY {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-80%);
    transform: translateY(-80%);
  }
  100% {
    opacity:1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@-webkit-keyframes showToY {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-80%);
    transform: translateY(-80%);
  }
  100% {
    opacity:1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
