.switch__label {
  position: relative;
  display: block;
  vertical-align: top;
  width: 80px;
  height: 30px;
  padding: 0;
  background-image: none;
  margin:0;
  border-radius: 18px;
  cursor: pointer;
}
.switch__input {
  width: 80px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.switch__data {
  position: relative;
  display: block;
  height: inherit;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: inherit;
  border: solid 2px #a8a8a8;
  box-shadow: none;
  color:#a8a8a8;
}
.switch__data:before, .switch__data:after {
  position: absolute;
  top: 50%;
  margin-top: -.5em;
  line-height: 1;
  -webkit-transition: inherit;
  -moz-transition: inherit;
  -o-transition: inherit;
  transition: inherit;
}
.switch__data:before {
  content: attr(data-off);
  right: 11px;
}
.switch__data:after {
  content: attr(data-on);
  left: 11px;
  color: #313ee7;
  opacity: 0;
}
.switch__input:checked ~ .switch__data {  
  border-color: #313ee7;
}
.switch__input:checked ~ .switch__data:before {
  opacity: 0;
}
.switch__input:checked ~ .switch__data:after {
  opacity: 1;
}
.switch__handle {
  position: absolute;
  border-radius: 100%;  
  top: 6px;
  left: 8px;
  background: #a8a8a8;
  width: 22px;
  height: 22px;
  box-shadow: none; 
}
.switch__handle:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -6px 0 0 -6px;
  width: 12px;
  height: 12px;
  border-radius: 6px;
  background: #eceeef;
}
.switch__input:checked ~ .switch__handle {
  left: 52px;
  background: #313ee7;
  box-shadow: none;
}
.switch__data, .switch__handle {
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}

