.grid--3col .grid__item--x2, .grid--4col .grid__item--x3, .grid--5col .grid__item--x2,  .grid--5col .grid__item--x3,  .grid--5col .grid__item--x4,  .grid--6col .grid__item--x2,  .grid--6col .grid__item--x3, .grid--6col .grid__item--x4, .grid--6col .grid__item--x5 {
   width: 100%;
}
.grid--2col .grid__item--padding, .grid--3col .grid__item.grid__item--padding,  .grid--3col .grid__item--x2.grid__item--padding, .grid--4col .grid__item.grid__item--padding, .grid--4col .grid__item--x3.grid__item--padding, .grid--5col .grid__item.grid__item--padding,  .grid--5col .grid__item--x2.grid__item--padding,  .grid--5col .grid__item--x3.grid__item--padding,  .grid--5col .grid__item--x4.grid__item--padding, .grid--6col .grid__item.grid__item--padding,  .grid--6col .grid__item--x2.grid__item--padding,  .grid--6col .grid__item--x3.grid__item--padding,  .grid--6col .grid__item--x4.grid__item--padding,  .grid--6col .grid__item--x5.grid__item--padding{
   width: calc(100% - 20px * 2);
   padding:20px;
}
.grid__title{
  font-size:18px;
}
.grid__text {
  padding-bottom:20px;
  line-height:22px;
}

/*       FOOTER      */
/*=====================*/
.footer__content{
  padding-top:50px;
}
ul.grid__list--fmenu li {
  display:block;
    float:left;
  margin:0 20px 10px 0;
}

/*  INTRO    */
/*---------------*/
.intro{
  width:100%;
  padding:20% 0 0 0;
}
.intro__title{
  padding:0 0 40px 0;
  font-size:80px;
  line-height:88px;
  text-align:center;
}
.intro__subtitle{
  font-size:38px;
  line-height:32px;
  padding:0 15% 20px 15%;
  text-align:center;
}
.intro__description{
  font-size:20px;
  padding:0 0 40px 0;
  text-align:center;
}
.intro__buttons--left{
   text-align:center;
}
.intro__buttons{
   padding:40px 0 0 0;
}
svg.svg-intro-bottom {
  height: 30%;
}
.intro-animation{
  width:90%;
  height:35%;
  float:left; 
  text-align:right;
  position:absolute;
  bottom:0;
  left:5%;
  z-index:222;
}
.intro-animation img{
  max-width:100%;
}
.intro__form{
  width:99%;
}

/*  ABOUT    */
/*---------------*/
.section__content--about{
  padding:30px 0;
}
.grid--about .grid__item{
  margin-bottom:30px;
}
.grid--about .grid__title{
  font-size:28px;
  line-height:36px;
}
.grid--about .grid__text{
  font-size:16px;
  line-height:24px;
}

/*  FEATURES   */
/*---------------*/
.section__content--features{
  padding:40px 0 20px 0;
}
.section--features{
  padding:0 0 40px 0;
}


/*  PRICING    */
/*---------------*/
.pricing__switcher{
  width:260px;
  margin:0 auto 40px auto;
}
.pricing__plan{       
  width: 100%;  
  padding:20px 0; 
  margin:0 0 10px 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  -moz-box-shadow: none;
}
.pricing__plan--popular {
  z-index: 2;
  top: 0px;
  padding:20px 0;
  -moz-box-shadow: 10px 0 15px -10px rgba(0, 0, 0, .45), -10px 0 15px -10px rgba(0, 0, 0, .45);
  -webkit-box-shadow: 10px 0 15px -10px rgba(0, 0, 0, .45), -10px 0 15px -10px rgba(0, 0, 0, .45);
  box-shadow: 10px 0 15px -10px rgba(0, 0, 0, .45), -10px 0 15px -10px rgba(0, 0, 0, .45);    
}
/*  TESTIMONIALS   */
/*---------------*/
 .testimonials{
  margin:0;
  padding:40px 0 80px 0; 
}
.testimonials__slide {
  text-align: center;
  width:100%;
  float:left;
}
.testimonials__text p{
  padding:0 0 20px 0;
  font-size:14px;
  line-height:22px;
}
.testimonials__source{
  font-weight:700;
  font-size:12px;
}
.testimonials__source a{
  padding:0 5px;
}
/*  CONTACT    */
/*---------------*/
.grid--contact{
  display:block;
}
.grid--contact .grid__item, .grid--6col .grid__item{
  margin-bottom:0px !important;
}
.grid__form{
  width:90%;
  padding:0 5%;
  position:relative;
}
.form__submit{
  margin:40px 0 0 0;
}
.svg-form-left{
  display:none;
}
/*  CTA    */
/*---------------*/
.section--cta{
  padding:20px 0;
}
svg.svg-cta-bottom {
  height: 20px;
}
/*  Modal  */
/*---------------*/
.modal__wrapper {
  top: 5%;
  width: 90%;
  margin-left: -45%;
}
.modal__wrapper--image {
  top: 10%;
  left: 14%;
  width: 86%;
  bottom:auto;
  margin-left: -7%;
}
.modal__content.modal__content--visible{
  display: block;
}
.modal__info{
  display:none;
}
.modal__form{
  width:90%;
  height:100%;
  display:block;
  padding:20px 5%;
}
.modal__content--login, .modal__content--forgot, .modal__content--signup{
  background-color:#fff;
}
.modal__image{
  height:auto;
}
.modal__image img{
  max-width:100%;
  max-height:auto;
}
.section__content--support{
  background:none;
  box-shadow:none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
}
.grid--support{
  width:100%; 
  display:block;
}
.grid--support .grid__item.grid__item--padding{
  background:none;
  width: 90%;
  padding:5%;
  text-align:center;
}
.grid--support .grid__more{
  float:none;
}
.grid--features .grid__item, .grid--more-features .grid__item{
   margin-right:0px;
   margin-bottom:40px; 
   width: 90%;
   padding:5%;
}

}

